.header {
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.header p {
  margin-left: 2em;
}

.challengeWrapper {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fill, minmax(180px, 1fr));
}

.radios {
  width: 180px;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0.5em 0.5em;
}

.radios input[type=radio] {
  display: none;
}

.challengeButton {
  background-color: #A2E5D4;
  height: 50px;
  width: 150px;
  border-radius: 3px;
  position: relative;
  margin-right: 20px;
  margin-bottom: 50px;
  float: right;
}

.challengeResult {
  width: 250px;
  display: grid;
  grid-template-rows: 25% 33% 42%;
  font-family: "Lato", sans-serif;
  color: #16324F;
}

.challengeStats {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
}

.challengeStats h1 {
  font-family: "Boogaloo", sans-serif;
  color: #16324F;
  font-size: 1.5em;
  background-color: #f4f4f4;
  border-radius: 50%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  padding: 8px;
  display: inline-block;
}

.secondaryInfo {
  display: inline-block;
  font-size: 1em;
  margin-top: 40px;
  align-self: end;
  text-align: right;
  color: #FED1B3;
}

.challengeStats {
  grid-area: 1 / 1 / 2 / 2;
  padding: 5px;
}

.answers {
  grid-area: 2 / 1 / 3 / 2;
  padding: 5px;
  color: #FED1B3;
  font-family: "Lato", sans-serif;
}

.note {
  grid-area: 3 / 1 / 4 / 2;
  padding: 2px;
  text-align: justify;
  color: #13293d;
  background-repeat: no-repeat;
