.occupiedClassroom .scoreCard p {
  font-family: "Boogaloo", sans-serif;
  font-size: 2.7em;
  color: #16324F;
}

.occupiedClassroom .studentSection ul {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-row-gap: 50px;
  grid-column-gap: 10px;
}

@media (max-width: 767px) {
  .emptyClassroom {
    padding: 1em;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(5, 1fr);
    grid-column-gap: 3px;
    grid-row-gap: 2px;
  }

  .emptyClassroom .titleSection {
    grid-area: 1 / 1 / 2 / 3;
  }

  .emptyClassroom .addStudentSection {
    grid-area: 3/ 2 / 4 / 4;
  }

  .occupiedClassroom {
    padding: 1em;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(5, auto);
    grid-column-gap: 3px;
    grid-row-gap: 2px;
  }

  .occupiedClassroom .titleSection {
    grid-area: 1 / 1 / 2 / 3;
    max-height: 8.375em;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

  .occupiedClassroom .addStudentSection {
    grid-area: 2 / 1 / 3 / 2;
    max-height: 8.375em;
  }

  .occupiedClassroom .scoreCard {
    grid-area: 2 / 2 / 3 / 3;
    max-height: 8.375em;
    margin-left: 80px;
  }

  .occupiedClassroom .scoreLabel {
    display: none;
  }

  .occupiedClassroom .classChart {
    grid-area: 3 / 1 / 5 / 3;
    max-height: 31.25em;
    min-height: 22.5em;
    margin-bottom: 20px;
  }

  .occupiedClassroom .studentSection {
    grid-area: 5 / 1 / 6 / 3;
  }
}

@media (min-width: 768px) {
  .emptyClassroom {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    grid-template-rows: repeat(11, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 5px;
  }

  .emptyClassroom .titleSection {
    grid-area: 2 / 2 / 3 / 5;
  }

  .emptyClassroom .addStudentSection {
    grid-area: 5 / 7 / 6 / 10;
  }

  .occupiedClassroom {
    background-color: rgba(162, 193, 186, 0.4);
    border: double #A6CBC7 8px;
    box-shadow: 0px 5px 8px -1px rgba(0, 0, 0, 0.4);
    border-top: 0px;
    border-bottom: 0px;
    padding: 2em;
    margin-right: 3em;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(5, auto);
    grid-column-gap: 1em;
    grid-row-gap: 1.5em;
  }

  .occupiedClassroom .titleSection {
    grid-area: 1 / 1 / 2 / 2;
    margin-bottom: 1em;
  }

  .occupiedClassroom .scoreCard {
    grid-area: 1 / 4 / 2 / 5;
  }

  .occupiedClassroom .scoreLabel {
    grid-area: 1 / 3 / 2 / 4;
  }

  .occupiedClassroom .addStudentSection {
    grid-area: 3 / 1 / 4 / 2;
  }

  .occupiedClassroom .classChart {
    grid-area: 2 / 1 / 3 / 5;
    max-height: 31.25em;
    border-radius: 10px 60px 10px 10px;
  }

  .occupiedClassroom .studentSection {
    grid-area: 4 / 1 / 6 / 5;
    margin-top: 1em;
  }
}

