  text-align: left;
  margin: 5px 5px;
  font-size: 25px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.9);
}

.face svg {
  margin-top: -34px;
}

.face svg > path {
  fill: #F6F9C2;
}

.face:nth-child(2) {
  -webkit-transform: rotateX(90deg) translate3d(0, 0, 90px);
          transform: rotateX(90deg) translate3d(0, 0, 90px);
  background-color: #cb6b45;
}

.face:last-child {
  -webkit-transform: rotateY(90deg) translate3d(0, 0, 90px);
          transform: rotateY(90deg) translate3d(0, 0, 90px);
  background-color: #264463;
}

.face:first-child {
  -webkit-transform: translate3d(0, 0, 90px);
          transform: translate3d(0, 0, 90px);
