.column {
  height: 300px;
  width: 15px;
}

.column path:first-child {
  -webkit-transition: fill .8s ease 100ms;
  transition: fill .8s ease 100ms;
}

.column path:nth-child(2) {
  -webkit-transition: fill .8s ease 500ms;
  transition: fill .8s ease 500ms;
}

.column path:nth-child(3) {
  -webkit-transition: fill .8s ease 900ms;
  transition: fill .8s ease 900ms;
}

.column path:nth-child(4) {
  -webkit-transition: fill .8s ease 1.2s;
  transition: fill .8s ease 1.2s;
}

.column path:nth-child(5) {
  -webkit-transition: fill .8s ease 1.4s;
  transition: fill .8s ease 1.4s;
}

.column path:nth-child(6) {
  -webkit-transition: fill .8s ease 1.7s;
  transition: fill .8s ease 1.7s;
}

.column path:nth-child(7) {
  -webkit-transition: fill .8s ease 2.1s;
  transition: fill .8s ease 2.1s;
}

.column path:nth-child(8) {
  -webkit-transition: fill .8s ease 2.6s;
  transition: fill .8s ease 2.6s;
}

.pagenotfound {
  min-height: 96vh;
  background: black;
  color: white;
}

.radioButton {
  margin: 1em;
  max-width: 180px;
  background-clip: padding-box;
  border-radius: 18px;
  border: none;
  background: #13293D;
  cursor: pointer;
}

.wrapper {
  padding: 5px 0px;
}

.activeNavLink {
  font-weight: 600;
  font-style: italic;
}

.icon {
  float: right;
  cursor: pointer;
}

@media (max-width: 640px) {
  .icon {
    padding-top: 10px;
  }
}

.iconClosed {
  color: #fff;
}

.iconOpen {
  color: #E3E5CC;
}

.studentNames {
  color: #E3E5CC;
  margin-top: 5;
  margin-left: 5px;
  padding: 10px 10px;
}

.sideBarRow {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-start;
}

.sideBarRow svg {
  display: inline;
  vertical-align: bottom;
  fill: #fff;
}

.sideBarRow:hover {
  color: #DDE589;
}

.sideBarRow:hover svg {
  display: inline;
  vertical-align: bottom;
  fill: #DDE589;
}

.item {
  max-width: 350px;
  max-height: 250px;
  display: grid;
  grid-template-columns: repeat(auto-fill, 70px) 50px;
  grid-template-rows: repeat(5, auto);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  align-items: end;
  margin: 1em 1em 0 0;
}

.card {
  grid-area: 1 / 1 / 6 / 5;
  padding-top: 20px;
  padding-bottom: 20px;
  background-color: rgba(134, 189, 177, 0.632);
  box-shadow: 11px 14px 8px -10px rgba(19, 61, 58, 0.75);
  border-radius: 15px;
}

.item:hover {
  -webkit-transform: scale(1.05, 1.05);
          transform: scale(1.05, 1.05);
  opacity: 1;
}

.item:hover .card {
  background-color: #7CC9B6;
}

.content {
  padding: 0px 15px 0 15px;
  grid-area: 1 / 1 / 6 / 4;
  font-family: "Lato", sans-serif;
  color: #16324F;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  font-size: 1.2em;
  min-height: 180px;
}

.content button svg {
  display: inline;
  fill: #ffffff;
}

.content h1 {
  font-weight: bold;
  font-size: 1.2em;
  display: inline;
}

.content p {
  font-size: 0.8em;
  padding-top: 0;
}

.content > button {
  z-index: 5;
  margin-top: 16px;
  margin-bottom: 0px;
  align-self: end;
  justify-self: center;
}

.score {
  font-family: "Boogaloo", serif;
  font-size: 1.8em;
  margin-left: 8px;
  border-radius: 45%;
  background-color: #f4f4f4;
  padding: 5px;
}

.letter {
  grid-area: 3 / 4 / 6 / 6;
  background-repeat: no-repeat;
  height: 100px;
  width: 100px;
  margin-bottom: -10px;
  margin-left: 10px;
  z-index: 4;
}

@media (max-width: 767px) {
  .item {
    width: 350px;
  }

  .card {
    width: 340px;
  }

  .letter {
    grid-area: 3 / 5 / 6 / 6;
    height: 150px;
    width: 150px;
  }

  .content p {
    font-size: 1em;
    padding-top: 0;
  }
}

.studentShow {
  padding: 2em;
}

.titleSection {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
}

.titleSection h1 {
  font-family: 'Boogaloo', cursive;
  font-size: 2.5em;
  color: #16324F;
}

.addChallenge svg {
  justify-self: flex-start;
  -webkit-filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.4));
  filter: drop-shadow(3px 3px 2px rgba(0, 0, 0, 0.4));
}

.addChallenge button {
  background-color: #16324F;
  font-family: "Lato", sans-serif;
  min-width: 200px;
  color: #fff;
  margin-bottom: 10px;
  padding: 1em 1em;
  border-radius: 5px;
  flex-direction: row;
  place-items: center;
  box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
}

.addChallenge button:hover {
  background-color: #8AC6AE;
}

.challengesWrapper {
  display: grid;
  grid-gap: 1em;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}

.challenge {
  width: 18em;
  height: 28em;
  padding: .8em;
  box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
  border-radius: 8% 8% 8% 8% / 8% 8% 8% 8%;
  margin-bottom: 1em;
  display: grid;
}

.challenge .challengeresult {
  grid-area: 1 / 2 / 1 / 2;
}

.challenge button {
  display: inline-block;
  grid-area: 1 / 2 / 1 / 2;
  align-self: start;
  margin-left: -40px;
  justify-self: end;
}

.studentChart {
  box-sizing: border-box;
  width: 100%;
  height: 450px;
  padding-top: 25px;
  background-color: #fff;
  border-radius: 15px 30px 15px 30px;
  box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
}

.alphabetChart {
  margin-top: 1em;
  background-color: #f4f4f4;
  padding: 0.5em;
  border-radius: 20px 40px 20px 40px;
  box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
}

.challengesTitle {
  margin-top: 1em;
  margin-bottom: 1.5em;
  display: flex;
  flex-direction: row;
  align-items: flex-end;
}

.challengesTitle > h2 {
  font-family: "Boogaloo", sans-serif;
  font-size: 2em;
  color: #1E446B;
}

.challengesTitle > svg {
  margin-bottom: 10px;
}

@media (max-width: 1027px) {
  .studentShow {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 9em auto;
    grid-column-gap: 4px;
    grid-row-gap: 4px;
  }

  .studentShow .titleSection {
    margin-top: 2em;
    padding: 1em;
