
.studentShow {
    padding: 2em;
  }
  
.titleSection {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
    h1{
      font-family: 'Boogaloo', cursive;
      font-size: 2.5em;
      color: #16324F;
    }
}
  
.addChallenge svg{
  justify-self: flex-start;
  filter: drop-shadow( 3px 3px 2px rgba(0, 0, 0, .4));
}
  
.addChallenge button{
    background-color: #16324F;
    font-family: "Lato", sans-serif;
    min-width: 200px;
    color: #fff;
    margin-bottom: 10px;
    padding: 1em 1em;
    border-radius: 5px;
    flex-direction: row;
    place-items: center;
    box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
}
  
.addChallenge button:hover{
    background-color:#8AC6AE;
}
  
.challengesWrapper {
    display: grid;
    grid-gap: 1em;
    grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
}
  
.challenge{
    width: 18em;
    height: 28em;
    padding: .8em;
    box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
    border-radius: 8% 8% 8% 8% / 8% 8% 8% 8%;
    margin-bottom: 1em;
    display: grid;
    .challengeresult{
      grid-area: 1 / 2 / 1 / 2;
    }
    
    button {
      display: inline-block;
      grid-area: 1 / 2 / 1 / 2;
      align-self: start;
      margin-left: -40px;
      justify-self: end;

    }
}

.studentChart {
    box-sizing: border-box;
    width: 100%;
    height: 450px;
    padding-top: 25px;
    background-color: #fff;
    border-radius: 15px 30px 15px 30px;
    box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
}
  
.alphabetChart {
    margin-top: 1em;
    background-color: #f4f4f4;
    padding: 0.5em;
    border-radius: 20px 40px 20px 40px ;
    box-shadow: -7px 5px 20px -10px rgba(19, 41, 61, 0.75);
}
.challengesTitle {
  margin-top: 1em;
  margin-bottom: 1.5em;
  display:flex;
  flex-direction: row;
  align-items: flex-end;
}
.challengesTitle > h2{
    font-family: "Boogaloo", sans-serif;
    font-size: 2em;
    color: #1E446B;
}
.challengesTitle > svg{
  margin-bottom: 10px;
}

@media (max-width: 1027px){              
  .studentShow {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 9em auto;
    grid-column-gap: 4px;
    grid-row-gap: 4px;
  }
  .studentShow .titleSection {
    margin-top: 2em; 
    padding: 1em;
    grid-area: 1 / 1 / 2 / 3;
    min-height: 5em;
    max-height: 6em;
  }
  
  .studentShow .addChallenge {
      grid-area: 2 / 1 / 3 / 3;
      min-height: 5em;
  }

  .studentShow .studentChart {
    grid-area: 3 / 1 / 5 / 4;
    margin-top: 1em;
  }

  .studentShow .alphabetChart {
    grid-area: 5/ 1 / 7 / 4;
    display: none;
  }

  .studentShow .challengeList {
    grid-area: 7 / 1 / 8 / 4;
  }

  .addChallenge svg{
    display: none;
  }

}

  
  @media (min-width: 1028px){          
      .studentShow {
        background-color: rgba($color: #A2C1BA, $alpha: 0.4);
        border: double #A6CBC7 8px;
        box-shadow: 0px 5px 8px -1px rgba(0,0,0,0.4);
        border-top: 0px;
        border-bottom: 0px;
        padding: 2em;
        margin-right: 3em;
        display: grid;
        grid-template-columns: minmax(300px, 1fr) auto repeat(3, 1fr);
        grid-template-rows: 140px repeat(auto-fit, minmax(300px, 1fr));
        grid-column-gap: 0.8em;
        grid-row-gap: 1em;
      }
  
      .studentShow .titleSection {
          grid-area: 1 / 1 / 2 / 2;
          min-height: 5em;
          max-height: 6em;
      }
  
      .studentShow .addChallenge {
          grid-area: 2 / 1 / 3 / 2;
      }
  
      .studentShow .studentChart {
          grid-area: 1 / 3 / 3 / 6;
          max-height: 31.25em;
          margin-top: 4em;
          border-radius: 50px 10px 50px 10px;
      }
  
      .studentShow .alphabetChart {
        grid-area: 3 / 1 / 5 / 6;
        padding: 1.5em;
        min-height: 20em;
      }
  
      .studentShow .challengeList {
        grid-area: 5 / 1 / 6 / 6;
      }
  }
  