.cube {
        width: 180px;
        height: 180px;
        text-align:center;
        transform-style: preserve-3d;
        transform: translateZ(-100px);
        transition: transform 800ms ease;
}
    
.face {
    font-family: "Lato", sans-serif;
    position: absolute;
    width: 100%;
    height: 100%;
    background-position: 0 100%;
    background-size: 100% 400%;
    backface-visibility: hidden;
    transition: background-position 1s;
    color: #13293D;
    border-radius: 18px;
    border: 10px #13293D solid;
    background-color: rgb(112, 125, 125); 
    box-shadow:inset 0 0 5px #ffffff; 
}

.face h1{
    font: bold 6em/160px 'Bree Serif', serif;
    text-shadow: 0 2px 0 rgba(255, 255, 255, 0.7);
}
.face p{
    font-size: 20px;
    margin-top: -25px;
    color: #F3F9A7;
}

.face > h2{
    text-align: left;
    margin: 5px 5px;
    font-size: 25px;
    text-shadow: 0 1px 0 rgba(255, 255, 255, 0.9);
}

.face svg{
    margin-top: -34px;
}

.face svg > path{
    fill: #F6F9C2;
}



.face:nth-child(2) {
    transform:rotateX(90deg) translate3d(0,0,90px);
    background-color: rgb(203, 107, 69);
}
  
.face:last-child  {
    transform:rotateY(90deg) translate3d(0,0,90px);
    background-color: #264463;

}

